<template>
    <div class="personInfo">
      <a href="javascript:;" @click="goback">返回首页</a>
      <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="用户姓名:" prop="name" style="width: 300px">
            <span>{{userData.name}}</span>
          </el-form-item>
          <el-form-item label="用户手机:" prop="tel" style="width: 300px">
            <el-input type="text" v-model="value" maxlength="11"></el-input>
          </el-form-item>
          <el-form-item label="部门:" prop="Depart" style="width: 300px">
            <span>{{userData.Depart}}</span>
          </el-form-item>
          <el-form-item label="职位:" prop="Position" style="width: 300px">
            <span>{{userData.Position}}</span>
          </el-form-item>
          <el-form-item label="登录密码:" prop="userPwd" style="width: 300px">
            <span>{{userData.userPwd}}</span>
            <el-button @click="showDialog"  style="margin-left: 15%" type="primary" icon="el-icon-edit" >修改</el-button>
          </el-form-item>
          <el-form-item v-if="isNewPwd" label="新密码:" style="width: 300px">
            <span>{{newPwd}}</span>
          </el-form-item>
          <el-form-item style="width: 300px">
            <el-button @click="cancel">取消</el-button>
            <el-button type="success" @click="submit">保存</el-button>
          </el-form-item>
        </el-form>

      <el-dialog
        title="修改密码"
        :visible.sync="dialogVisible"
        width="300px"  height="400px">
        <el-form ref="newPwd" >
          <el-form-item>
            <el-input class="newPwd" type="password" placeholder="输入新密码"	 v-model="newPwd1"> </el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="newPwd2" type="password" placeholder="确认新密码"> </el-input>
          </el-form-item>
          <span v-if="error1" style="color: red" >两次密码输入不一致</span>
          <span v-if="error2" style="color: red" >密码不能为空</span>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="savePwd">保存</el-button>
         </span>
      </el-dialog>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "person-info",
        data() {
            return {
              form: {},
              userData: {},
              value: '',
              dialogVisible: false,
              newPwd: '',
              isNewPwd: false,
              newPwd1: '',
              newPwd2: '',
              error1: false,
              error2: false,
            }
        },
      components:{
      },
      mounted(){
          this._initUserInfo();
      },
      methods:{
          _initUserInfo(){
            let userId = localStorage.getItem('userId');
            axios.post('/users/again',{
              userId: userId
            }).then( resposner=>{
              let res = resposner.data;
              if (res.status ==='0'){
                  this.userData = res.result;
                  this.value = this.userData.tel;
              }else {
                alert("用户名错误！请重新登录")
              }
            })
          },
        // 提交保存
        submit(){

        },
        showDialog(){
          this.dialogVisible = true;
          this.error = false;
        },
        // 保存密码
        savePwd(){
            if ((!this.newPwd1) || (!this.newPwd2)){
              this.error2 = true;
              this.error1 = false;
            } else {
              if (this.newPwd1 === this.newPwd2){
                this.newPwd = this.newPwd1;
                this.isNewPwd = true;
                this.dialogVisible = false;
              }else {
                this.error1 = true;
                this.error2 = false;
              }
            }
        },
        // 取消页面
        cancel(){
            this.$router.push({name: 'home'})
        },
        goback(){
            this.$router.push({name: 'home'})
        }

      }
    }
</script>

<style scoped>
  .personInfo{

  }
  .newPwd{
    margin: 20px auto;
  }

</style>



